.sign-up-form {
   padding: 140px 0px 0px 0px;
   &__title {
      &:not(:last-child) {
         margin-bottom: 50px;
      }
   }
   &__body {
      display: flex;
      flex-direction: column;
      align-items: center;
   }
   &__form {
      max-width: 380px;
      width: 100%;
   }
}
